const isPhoneNum = phone => /^1[3456789]\d{9}$/.test(phone);
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';

import {
	debounce
} from '@/common/utils.js'
export default {
	data() {
		return {
			numFocusStatus: false, // 数量是否获得焦点
			numErrTip: '', // 数量错误信息
			coloe: '',// 数量的值
			phoneFocusStatus: false, // 手机号是否获得焦点
			numFocusStatus: false, // 数量是否获得焦点
			phoneErrTip: '', // 手机号错误信息
			tel: '',// 手机号的值
			title: '',
			way: '',
			id: '',
			info: {},
			cool:'',
			machine_num:'',
			n: true,
			phone:'',
			numberList: [],
			length: 6,
			type: 'number',
			passIn: false,
			password: '',
		};
	},
	components: {
		keyboardPackage,
		passwordInput
	},
	onLoad(options) {
		console.log(options);
		var title = '';
		if (options.type === '1') {
			title = '转让';
		} else if (options.type === '2') {
			title = '出售';
		} else {
			return false;
		}
		uni.setNavigationBarTitle({
			title: '服务器' + title
		});
		this.title = title;
		this.info = JSON.parse(options.info);
		this.id = parseInt(this.info.id); 
		this.seere=parseInt(this.info.lens);//数量
		this.way = options.type;
		
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord()
		
	},
	methods: {
		formItemFocus(type) {
			switch (type) {
				case 'phone':
					this.phoneFocusStatus = true;
					break;
				case 'num':
					this.numFocusStatus = true;
					break;
				default:
					break;
			}
		},
		formItemBlur(type) {
			switch (type) {
				case 'phone':
					this.phoneFocusStatus = false;
					break;
				case 'num':
					this.numFocusStatus = false;
					break;
				default:
					break;
			}
		},
		clo: function() {
		    this.passIn = false;
		    this.$refs['number'].close();
		    this.numberList.length = 0;
		},
		onChange(e) {
		    if (e.show == false) {
		        this.passIn = false;
		    }
		    this.numberList.length = 0;
		},
		onDelete() {
		    this.numberList.pop();
		},
		onConfirm() {
		    if (this.numberList.length != 6) {
		        uni.showToast({
		            title: '请输入六位交易密码！',
		            duration: 2000,
		            icon: 'none'
		        });
		    }
		},
		submitForm: function() {
			this.linkToTransfer()
		},
		linkToTransfer: debounce(function() {
			
			if (!isPhoneNum(this.phoneVal)) {
				this.phoneErrTip = '手机号不正确';
			} else {
				this.phoneErrTip = '';
			}
			if (this.numVal=='' || isNaN(this.numVal) || this.numVal>parseFloat(this.seere)) {
				this.numErrTip = '数量不可用';
			}else{
				this.numErrTip = '';
			}
			if (this.phoneErrTip || this.numVal=='' || isNaN(this.numVal) || this.numVal>parseFloat(this.seere)) {
				return;
			}
			this.passIn = true;
			this.$refs['number'].open();
			this.onInput(val);
			
		}, 500, true),
		
		onInput(val) {
			var that = this;
			that.numberList.push(val);
			console.log(that.way)
			that.password = that.numberList.join().replace(/,/g, '');
			if (that.numberList.length >= that.length) {
				uni.request({
					url: this.url + 'usermachine/transfer/',
					method: 'POST',
					data: {
						way: that.way,
						number: that.numVal,
						mobile: that.phoneVal,
						password: that.password,
						type: that.id
					},
					header: {
						Authorization: 'JWT' + ' ' + that.global_.token
					},
					success(res) {
						console.log(that.way)
						console.log(that.numVal)
						console.log(that.phoneVal)
						console.log(that.password)
						console.log(that.id)
						console.log(res)
						if (res.statusCode == 200) {
							uni.redirectTo({
								url: '../sful/sful?numb=' + that.phoneVal + '&cool=' + that.numVal + '&way=' + that.way
							});
						}
						if (res.statusCode == 400) {
							that.$refs.wrong.flag = false;
							var n = res.data.data.err_num;
							var s = 5 - n;
							that.$refs.wrong.tip = '密码错误，剩余' + s + '次机会';
						}
						if (res.statusCode == 423) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '交易密码已锁定,请在今日24:00后进行交易',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 302) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '不允许出售或者转让经销商',
								icon: 'none'
							});
						}
						if (res.statusCode == 204) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '不允许转让普通用户',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 278) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '该用户不存在',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 305) {
							uni.showToast({
								title: '交易发生错误，请重新创建',
								icon: 'none'
							});
						}
						var page = getCurrentPages().pop();
						if (page == undefined || page == null) return;
						page.onLoad();
					}
				});
				this.numberList.length = 0;
			}
		},
	}
};
